<template>
<div id="head">
	<el-row class="head-wrap">
		<el-col :offset= "2" :span="2">
			<div><img class="logo" src="../assets/logo.jpg"></div>	
		</el-col>
		<el-col :span="20">
			<h1 class="com-name">山东财源和信节能工程有限公司</h1>
		</el-col>
	</el-row>
	<el-row :span="20">
		<el-col :span="24" class="nav-bg">
			<el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
			  <el-menu-item index="1" class="head-link"><router-link to="Main">首页</router-link></el-menu-item>
			  <el-menu-item index="3" class="head-link"><router-link to="About">关于我们</router-link></el-menu-item>
			  <el-menu-item index="3" class="head-link"><router-link to="About">联系我们</router-link></el-menu-item>
			</el-menu>
		</el-col>
	</el-row>
	<el-row>
		<el-col :span="24">
			<div class="grid-content">
		        <el-carousel indicator-position="outside">
		          <el-carousel-item v-for="item in sliders" :key="item">
		            <img :src="item">
		          </el-carousel-item>
		        </el-carousel>
		      </div>
		</el-col>
	</el-row>
</div>	
</template>
<script>
import {mapActions, mapState} from 'vuex'

export default {
  data() {
    return {
      search: '',
      activeIndex: '1',
      sliders:[
      	require('../assets/banner-home-1.jpg'),
      	require('../assets/banner-home-2.jpg'),
      	require('../assets/banner-home-3.jpg')
      ]
    }
  },
  computed: {
	...mapState(['userInfo'])
  },
  methods: {
  	...mapActions(['logout']),
    handleIconClick(ev) {
      console.log(this.search);
    },
    handleSelect(key,keyPath){
    	console.log(key,keyPath);
    }
  }
}
</script>
<style lang="less" scoped>
	@import '../style/mixin';
	.head-wrap{
		.wh(960px,102px);
		margin-top: 10px;
		margin-bottom: 0px;
		//margin: 0px auto;
		.user-info{
			height:102px; 
			line-height:102px;
			.welcome{
				margin-right:5px; 
				color:red;
			} 
		}
	}
	.nav-bg{
		background: #4682B4;
		.el-menu-demo{
			background:none; 
			width:960px; 
			margin:0 auto; 
		}
	}
	.logo {
		width: 50px;
		height: 50px;
		max-width: 100%;
		max-height: 100%;
		margin-top: 12px;
	}
	.com-name {
		color: #333;
  	font-weight: bold;
	}
	.head-link {
		color: #333;
  	font-weight: bold;
	}
</style>



